import React, {useState, useEffect} from 'react';
import cn from 'classnames';
import {Button, Checkbox, Form, Input, Switch, Select} from 'antd';
import add from '../../../common/images/auth/add.svg';
import photo from '../../../common/images/auth/photo.svg';
import {open} from "../../../common/images/partners";
import {api} from '../../../utils/api';
import Avatar from '../../../utils/avatar';
import {SelectItem} from '../../../utils/supports';

export const SecondStep = ({step, setStep, setFinish, initials, name, language, width}) => {
  const [form] = Form.useForm();
  const [src, setSrc] = useState(null)
  const [selectLanguage, setSelectLanguage] = useState(null)
  const [tags, setTags] = useState({})
  const [communication, setCommunication] = useState({})
  const [switchForm, setSwitchForm] = useState(false)
  const [linkText, setLinkText] = useState('')
  const [inputText, setInputText] = useState('')
  const [links, setLinks] = useState([])

  const dropLink = (link) => setLinks(links.filter(l => l !== link))

  const onFinish = values => {
    try {
      api.profileUpdate({...values, avatar: src, selectLanguage, tags, communication, switch: switchForm})
      setStep(3)
    } catch (e) {
    }
    setFinish({...values, avatar: src, selectLanguage, tags, communication, switch: switchForm})
  };

  const onInputText = e => {
    setLinkText(e.target.value)
    setInputText(e.target.value)
  }
  const onLinks = () => {
    setLinks([...links, linkText])
    setInputText('')
  }

  useEffect(() => {
    window.scrollTo(0, 0)
  }, []);

  return (
    <div className='second-step'>
      <div className='second-step__wrapper'>
        <Avatar src={src} setSrc={setSrc} initials={initials} icon={photo}/>
        <h3 className='second-step__name'>{name}</h3>
      </div>
      <Form
        form={form}
        name='register'
        onFinish={onFinish}
        scrollToFirstError>
        <div className='steps'>
          <div className={cn('step', {'current-step': step === 2})}/>
        </div>

        <Form.Item
          name='language'
          label='Language'>
          <Select className='ant-select-customize-input' defaultValue='English' onChange={setSelectLanguage} placeholder='Please select a Language'>
            {language.map(l => <Select.Option value={l.toLowerCase()}>{l}</Select.Option>)}
          </Select>
        </Form.Item>

        <Form.Item name='university' label='University'>
          <Input placeholder='Enter school name'/>
        </Form.Item>

        <Form.Item name='faculty' label='Faculty'>
          <Input placeholder='Enter faculty name'/>
        </Form.Item>

        <Form.Item name='year-of-end' label='Graduation year'
        rules={[{
          validator: (_, value) => {
            if (/^(19|20)\d{2}$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Please enter only number!')
          }
        }]}>
          <Input placeholder='0000' maxLength={4}/>
        </Form.Item>

        <Form.Item name='social-media-contacts' label='Social media'>
          <div className='flex-align-center' style={{position: 'relative'}}>
            <Input placeholder='Enter links' value={inputText} onChange={onInputText}/>
            <img className='form__link-add' src={width < 681 ? open : add} alt='add new link' onClick={onLinks}
                 style={{position: 'absolute', right: width < 681 ? '12px' : '-80px', cursor: 'pointer'}}/>
          </div>
        </Form.Item>

        <div className='links'>
          {!!links.length && links.map((link, i) => <SelectItem key={link + i} option={link} dropSelect={dropLink}/>)}
        </div>

          <Form.Item className='border-none' name='tags' label='Tags' onChange={e => setTags({...tags, [e.target.value]: e.target.checked})}>
            <div><Checkbox value='Networking'>I'm preparing for case interview</Checkbox></div>
            <div><Checkbox value='Preparing for an interview'>I want to participate in case competition</Checkbox></div>
            <div><Checkbox value='I am looking for a team'>I want to develop my problem solving skills</Checkbox></div>
            <div><Checkbox value='I am looking for a team'>I want to extend my professional network</Checkbox></div>
          </Form.Item>

        <Form.Item name='about-me' label='Tell about yourself'>
          <Input.TextArea placeholder='Free format'/>
        </Form.Item>

        <div className='mb22'>
          <Form.Item className='border-none' onChange={e => setCommunication({...communication, [e.target.value]: e.target.checked})}
                     label='I prefer to contact by'>
            <div><Checkbox value='E-mail'>E-mail</Checkbox></div>
            <div><Checkbox value='Phone / Messengers'>Phone / Messengers</Checkbox></div>
            <div><Checkbox value='Social media (Facebook, LinkedIn etc.)'>Social media (Facebook, LinkedIn etc.)</Checkbox></div>
          </Form.Item>
        </div>
        <Form.Item className='border-none'>
          <div className='search-access'>
            <div className='search-access__title'>Available</div>
            <Switch onChange={setSwitchForm}/>
          </div>
          <div className='search-access__text'>By clicking here you open your
            page for other users and you can
            find buddies using our search tool.
          </div>
        </Form.Item>
        <Form.Item className='border-none'>
          <Button className='blue mt30' htmlType='submit' shape='round'>
            Save
          </Button>
          <Button className='mt20' htmlType='submit' shape='round'>
            Finish later
          </Button>
        </Form.Item>
      </Form>

    </div>
  );
};
