import React, {useState} from 'react';
import {Switch, Input} from 'antd';
import Star from '../icon/Star';
import {pdf} from '../../common/images/account';
import {closeMimi} from '../../common/images/partners';
import {close} from '../../common/images/materials';
import './index.scss';

export const File = ({title, size, file}) =>
  <div className='file'>
    {/*<a href={'#'} style={{display: 'flex'}} download={file}>*/}
    <img src={pdf} alt='pdf'/>
    <div className='file__info'>
      <h5 className='file__info-title'>{title}</h5>
      <div className='file__info-size'>{size}</div>
    </div>
    {/*</a>*/}
  </div>

export const SelectItem = ({option, dropSelect}) => {
  const sliced = option.slice(0, 15);

  return <div className='select-item'>
    <div className='select-item__text'>
      {option.isSearch ? option.search : sliced.length < option.length ? sliced + '...' : option}
    </div>
    <div className='select-item__drop' onClick={() => dropSelect(option, option.isSearch && option.search)}>
      <img src={closeMimi} alt='close'/>
    </div>
  </div>
}

const defPhotos = [
  {
    id: 1,
    full: 'https://bucketlistdot.files.wordpress.com/2018/01/5877798-cherry-blossom-pictures.jpg',
    small: 'https://lh3.googleusercontent.com/20AcRFsZgaydNl-DNtCW99UvGnk1VDu7uHvyfRQbmYoqVlbZA-CB2eD_rSIhze3kLpI=w80'
  },
  {
    id: 2,
    full: 'http://pm1.narvii.com/6928/ba9a83fe48593f8fb02d19bffb231105b4ca0ec2r1-1024-768v2_uhq.jpg',
    small: 'https://lh3.googleusercontent.com/dqxf2xEf-HlFbjyhtP7-KTn778RgrBvXxTzAuGMGghZQuzfRG66hc3NYdiv10cJvXg=w80'
  },
  {
    id: 3,
    full: 'https://bucketlistdot.files.wordpress.com/2018/01/5877798-cherry-blossom-pictures.jpg',
    small: 'https://lh3.googleusercontent.com/20AcRFsZgaydNl-DNtCW99UvGnk1VDu7uHvyfRQbmYoqVlbZA-CB2eD_rSIhze3kLpI=w80'
  }
];

export const CaseInfo = ({text, files, photos, title}) => {
  const [showPopup, setShowPopup] = useState(false)
  const [currentImage, setCurrentImage] = useState(null)
  const onShowImage = (img) => {
    setShowPopup(true)
    setCurrentImage(img)
  }

  return <>
    <div className='case-text'>
      <p>{text}</p>
    </div>
    <div className='case-files'>
      <div className='case-files__title'>{title}</div>
      <div className='case-files__main'>
        {files.map(({title, size, file}, i) => <File key={title + i} title={title} size={size} file={file}/>)}
      </div>
    </div>
    {!photos && <div className='case-files'>
      <div className='case-files__title'>Images</div>
      <div className='case-files__main photo-files'>
        {defPhotos.map(({id, full, small}, i) =>
          <img className='photo-file' key={small + i} onClick={() => onShowImage({id, full})} src={small} alt='загруженные изображения'/>)}
      </div>
    </div>}
    {showPopup &&
    <Popup image={currentImage.full} id={currentImage.id} setCurrentImage={setCurrentImage} photos={defPhotos} setShowPopup={setShowPopup}/>}
  </>
}

export const TextArea = ({text, setText, title}) =>
  <div className='textarea'>
    <div className='textarea__title'>{title || 'Your Feedback'}</div>
    <Input.TextArea placeholder='Please give detailed feedback' value={text} onInput={e => setText(e.target.value)}/>
    <div className='textarea__words'>Symbols {text.length} / 5000</div>
  </div>

export const Stars = ({stars, setCurrentStar}) => {
  return Array(7).fill(0)
    .map((_, i) => i < stars
      ? <div onClick={() => setCurrentStar(++i)}><Star key={i} fill='#feda00'/></div>
      : <div onClick={() => setCurrentStar(++i)}><Star key={i} onClick={setCurrentStar}/></div>)
}

export const SearchAccess = () =>
  <>
    <div className='search-access'>
      <div className='search-access__title'>Available for search</div>
      <Switch/>
    </div>
    <div className='search-access__text'>
      Open profile for search to allow other participants in the platform
      to view your details, request that you add them to friends, and solve cases together.
    </div>
  </>

export const Popup = ({image, id, photos, setCurrentImage, setShowPopup}) => {
  const nextID = id + 1;
  const prevID = id - 1;
  return (
    <div className='popup'>
      <div className='popup-container'>
        <div className='popup__close' onClick={() => setShowPopup(false)}>
          <img src={close} alt='close'/>
        </div>
        <div className='popup__video'>
          <div className='popup__video-block'>
            {image
              ? <div className='popup-slider'>
                <div className='popup-slider-arrow-left'
                     onClick={() => setCurrentImage(photos.filter(p => p.id === prevID && p)[0])}
                     style={id > 1 ? {} : {display: 'none'}}/>
                <img src={image} alt='loaded image'/>
                <div className='popup-slider-arrow-right'
                     onClick={() => setCurrentImage(photos.filter(p => p.id === nextID && p)[0])}
                     style={id < photos.length ? {} : {display: 'none'}}/>
              </div>
              : <iframe className='popup__video-iframe' width='1000' height='555' src='https://www.youtube.com/embed/d84nAXalLck' frameBorder='0'
                        allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture' allowFullScreen/>
            }
          </div>
        </div>
      </div>
    </div>
  )
}
