import React, {useEffect, useState, useRef} from 'react';
import {Form, Input, Button} from 'antd';
import {connect} from 'react-redux';
import {NavLink} from 'react-router-dom';
import {api} from '../../../utils/api';
import {NoVisible, Visible} from '../../../utils/icon/eye';
import Layout from '../../../utils/layout/service';
import {setCurrentPage, CURRENT_PAGE} from '../../../redux/reducers/page';
import {SET_USER_ID, setUserId, setUserIsAuth, USER_IS_AUTH} from '../../../redux/reducers/user';
import {Key} from '../../../utils/icon/key';

// data: {id: 2, first_name: "", last_name: "", email: "dark@gmail.com"} || data: {result: "error"}

const Login = ({setCurrentPage, setUserIsAuth, isMenuShow, setIsMenuShow, setUserId}) => {
  const [isVisible, setIsVisible] = useState(false);

  const onUser = (id) => {
    setUserId(SET_USER_ID, id)
    setUserIsAuth(USER_IS_AUTH, true)
  }

  const onFinish = values => {
    const res = api.login(values);
    // res.then(r => r.data.result === 'error' ? console.error('Ошибка входа') : onUser(r.data.id))
    setUserIsAuth(USER_IS_AUTH, true)
  };

  const passwordRef = useRef(null)

  useEffect(() => {
    isVisible
      ? passwordRef.current.input.type = 'text'
      : passwordRef.current.input.type = 'password'
  }, [isVisible])

  useEffect(() => setCurrentPage(CURRENT_PAGE, 'Log in'), [])

  return (
    <Layout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
      <div className='login'>
        <h1 className='title'>Log in</h1>
        <Form
          name='login'
          className='login-form'
          initialValues={{remember: true}}
          onFinish={onFinish}>

          <Form.Item
            name='email'
            label='Email'
            rules={[
              {
                type: 'email',
                message: 'The input is not valid E-mail!',
              },
              {
                required: true,
                message: 'Please input your E-mail!',
              },
            ]}>
            <Input placeholder='Email'/>
          </Form.Item>

          <Form.Item
            className='login-password'
            name='password'
            label='Password'
            rules={[{required: true, message: 'Please input your password!'}]}>
            <Input.Password placeholder='Password'
                            ref={passwordRef}
                            iconRender={visible => (isVisible
                              ? <Visible isVisible={isVisible} setIsVisible={setIsVisible}/>
                              : <NoVisible isVisible={isVisible} setIsVisible={setIsVisible}/>)}
            />
          </Form.Item>

          <div className='login-form-forgot'>
            <NavLink className='login-link' to='/forgot'>
              <Key className='site-form-item-icon'/>
              Forgotten your password?
            </NavLink>
          </div>

          <Form.Item className='border-none'>
            <Button className='blue mt30' htmlType='submit' shape='round' size='large'>
              Log in
            </Button>
          </Form.Item>
          <div className='auth-link'>
            <NavLink to='/register' className='auth-arrow-icon'>No account? Sign up</NavLink>
          </div>

        </Form>
      </div>
    </Layout>
  );
};

export default connect(null, {setCurrentPage, setUserIsAuth, setUserId})(Login);
