import React from 'react';
import {NavLink} from 'react-router-dom';
import {Button} from 'antd';
import Layout from '../../../utils/layout/service';

const MailNotConfirmed = () => {
  return (
    <Layout>
      <div className='mail-not-confirmed'>
        <h1 className='title'>Your email address is not verified yet.</h1>
        <div className='mail-not-confirmed__text'>
          We sent you an email to verify your email address<br/> (check your spam or junk folder if you did not receive this email).
        </div>
        <Button className='blue'><NavLink to='/'>Go to mainpage</NavLink></Button>
      </div>
    </Layout>
  );
};

export default MailNotConfirmed;
