import React, {useState, useEffect, useRef} from 'react';
import cn from 'classnames';
import {Button, Input, Upload} from 'antd';
import {PaperClipOutlined} from '@ant-design/icons';
import {NavLink} from 'react-router-dom';
import file from '../../../common/files/file.pdf';
import {dislike, like} from '../../../common/images/case-challenge';
import {videoPreview} from '../../../common/images/case-challenge';
import {CaseInfo, Popup, Stars, TextArea} from '../../../utils/supports';
import './index.scss';

export const YourSolution = ({setCurrentStep}) => {
  const [counter, setCounter] = useState(0)
  const [files, setFiles] = useState(null)
  const [text, setText] = useState('')


  const onChange = file => {
    setCounter(file.fileList.length)
    setFiles(file.fileList)
  }

  return (
    <div className='solution'>
      <div className='solution__title'>
        Answer
      </div>
      <div className='solution__main'>
        <div className='p-textarea'>
          <Input.TextArea placeholder='Text' value={text} onInput={e => setText(e.target.value)} autoSize={{minRows: 8, maxRows: 35}}/>
        </div>
        <div className='solution__main-upload'>
          <Upload className='upload-list-inline' accept='.docx, application/pdf' customRequest={null} maxCount={5} onChange={onChange}>
            <PaperClipOutlined/>Attach files {counter}/5 <span className='limitation-file'>up to 29 MB in formats: pdf, docx</span>
          </Upload>
        </div>
        <Button className='blue' disabled={!text.length} onClick={() => setCurrentStep(2)}>Save</Button>
      </div>
    </div>
  )
}

const files = [{title: 'Скачки напряжения.pdf', size: '76 мб', file}, {title: 'Скачки напряжения.pdf', size: '76 мб', file}, {title: 'Скачки напряжения.pdf', size: '76 мб', file}];
const text = 'Крупная компания, входящая в топ в своем сегменте российского рынка, строит центр обработки данных (ЦОД) для заказчика – государственной структуры. Заказчик для подрядчика очень важен – это системный, стратегический, ключевой клиент. Работа заказчика влияет на огромное количество компаний, физических лиц и государственных органов. Заказчик постоянно находится на контроле у вышестоящих органов. Проект ЦОД для заказчика тоже стратегически важен. В проект входят серверная, инфраструктура, система бесперебойного и резервного питания. Смета составляет сотни миллионов рублей.'
export const Solutions = ({partners = [], setCurrentStep}) => {
  const [isShow, setIsShow] = useState(false)
  const [inputText, setInputText] = useState('')
  const [currentPartner, setCurrentPartner] = useState(partners[0])
  const [currentStar, setCurrentStar] = useState(6)

  return (
    <div className='solutions flex-start-between'>
      <div className='solutions__partners'>
        <div className='solutions__partners-title'>Case solutions</div>
        <div className='solutions__partners-lists'>
          <ul>
            {partners.map(p =>
              <li className={cn({'select': p.name === currentPartner.name})} onClick={() => setCurrentPartner(p)}>
                {p.name}
              </li>
            )}
          </ul>
        </div>
      </div>
      <div className='solutions__main'>
        <div className='solutions__main-partner-profile'><NavLink to={`/user/${currentPartner.link}`}>{currentPartner.name}</NavLink></div>
        <div className='solutions__main-partner-decision'>
          {isShow ?
            <><p>{currentPartner.decision || text}</p> <span onClick={() => setIsShow(false)}>(Hide text)</span></> :
            <div className='default' onClick={() => setIsShow(true)}>
              Answer
            </div>}
        </div>

        <div className='solutions__main-files'>
          <CaseInfo title='Files' photos={true} files={files}/>
        </div>

        <TextArea text={inputText} setText={setInputText}/>

        <div className='solutions__main-feedback'>
          <div className='solutions__main-feedback__title'>
            Your score
          </div>
          <div className='flex-start-between'>
            <div className='solutions__main-feedback__appraise'>
              <div className='solutions__main-feedback__appraise-stars'>
                <Stars stars={currentStar} setCurrentStar={setCurrentStar}/> <span>{currentStar}/7</span>
              </div>
              <Button className='blue' onClick={() => setCurrentStep(3)}>Send</Button>
            </div>
            <div className='solutions__main-feedback__info flex-start-between'>
              <div className='solutions__main-feedback__info-opinion'>{currentStar}</div>
              <p className='solutions__main-feedback__info-text'>
                Case correctly solved, calculations and
                conclusions correct and rational. Slide
                visualization concise and professional,
                story clear and logical.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export const Feedback = ({partners = [], setCurrentStep}) => {
  const [isShow, setIsShow] = useState(false)
  const [currentPartner, setCurrentPartner] = useState(partners[0])
  const [currentStar, setCurrentStar] = useState(6)
  return (
    <div className='feedback'>
      <div className='flex-start-between'>

        <div className='feedback__partners'>
          <div className='feedback__partners-title'>Feedback from</div>
          <div className='feedback__partners-lists'>
            <ul>
              {partners.map(p =>
                <li className={cn({'select': p.name === currentPartner.name})} onClick={() => setCurrentPartner(p)}>
                  {p.name}
                </li>
              )}
            </ul>
          </div>
        </div>
        <div className='feedback__main'>
          <div className='feedback__main-partner-profile'><NavLink to={`/profile/${currentPartner.link}`}>{currentPartner.name}</NavLink></div>
          <div className='feedback__main-partner-decision'>
            <p>
              Крупная компания, входящая в топ в своем сегменте российского рынка, строит центр обработки данных (ЦОД) для заказчика – государственной структуры. Заказчик для
              подрядчика очень важен – это системный, стратегический, ключевой клиент. Работа заказчика влияет на огромное количество компаний, физических лиц и государственных
              органов. Заказчик постоянно находится на контроле у вышестоящих органов. Проект ЦОД для заказчика тоже стратегически важен. В проект входят серверная, инфраструктура,
              система бесперебойного и резервного питания. Смета составляет сотни миллионов рублей.
            </p>
          </div>
          <div className='feedback__main-appraise'>
            <div className='feedback__main-appraise__title'>Your score</div>
            <div className='feedback__main-appraise__stars'>
              <Stars stars={currentStar} setCurrentStar={setCurrentStar}/> <span>{currentStar}/7</span>
            </div>
          </div>
        </div>
      </div>
      <div className='flex-center'>
        <div className='feedback__appraise'>
          <div className='feedback__appraise-title'>Was it helpful?</div>
          <div className='feedback__appraise-btn'>
            <Button onClick={() => setCurrentStep(4)}>Yes<img className='like' src={like} alt='like'/></Button>
            <Button>No<img className='dislike' src={dislike} alt='dislike'/></Button>
          </div>
        </div>
      </div>
    </div>
  )
}

export const LastStep = ({setCurrentStep}) => {
  const [text, setText] = useState('')
  const [checked, setChecked] = useState('')
  return (
    <div className='last-step'>
      <div className='last-step__score'>
        <div className='last-step__score-title'>How did you like this case?</div>
        <div className='last-step__score-btn'>
          <Button className={cn('case-red', {'checked': checked === 'red'})} onClick={() => setChecked('red')}>
            Didn’t like it
          </Button>
          <Button className={cn('case-yellow', {'checked': checked === 'yellow'})} onClick={() => setChecked('yellow')}>
            It was Ok
          </Button>
          <Button className={cn('case-green', {'checked': checked === 'green'})} onClick={() => setChecked('green')}>
            Great, love it
          </Button>
        </div>
      </div>
      <TextArea title='Your comments' text={text} setText={setText}/>
      <Button className='blue' onClick={() => setCurrentStep(5)}>Send</Button>
    </div>
  )
}

export const CaseClose = () => {
  const [showPopup, setShowPopup] = useState(false)
  return (
    <div className='case-close'>
      <div className='case-close__title'>Case completed</div>
      <div className='case-close__main'>
        <div className='case-close__main-title'>McKinsey Consultant</div>
        <div className='case-close__main-video' onClick={() => setShowPopup(true)}>
          <img src={videoPreview} alt='preview'/>
        </div>
      </div>
      {showPopup && <Popup setShowPopup={setShowPopup}/>}
    </div>
  )
}
