import axios from 'axios';
const baseUrl = '194.87.92.118:5000/api/v1/'
export const api = {
  login: (data) => axios.post(baseUrl+'user/auth', data),
  createUser: (data) => axios.post(baseUrl+'user/create', data),
  profileUpdate: (data) => axios.post(baseUrl+'user/profile/update', data),
  recovery: (data) => axios.post(baseUrl+'user/password/recovery', data),
  updatePassword: (data) => axios.post(baseUrl+'user/password/change', data),
}
