import React from 'react';
import {Route} from 'react-router-dom';
import Forgot from './Forgot';
import Login from './Login';
import MailNotConfirmed from './MailNotConfirmed';
import PasswordRecovery from './PasswordRecovery';
import Register from './Register';

const Auth = ({isMenuShow, setIsMenuShow, language}) => {
  return (
    <>
      <Route path='/login' exact={true} render={() => <Login isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/forgot' exact={true} render={() => <Forgot isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/mail-not-confirmed' exact={true} render={() => <MailNotConfirmed isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/password-recovery' exact={true} render={() => <PasswordRecovery isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/register' exact={true} render={() => <Register isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow} language={language}/>}/>
    </>
  );
};

export default Auth;
