import React, {useState} from 'react';
import {NavLink} from 'react-router-dom';
import {Button} from 'antd';
import {videoPreview} from '../../../common/images/case-challenge';
import {arrow} from '../../../common/images/partners';
import {Popup} from "../../../utils/supports";

export const ThreeStep = () => {
  const [showPopup, setShowPopup] = useState(false)
  return (
    <div className='three-step'>
      <h1 className='three-step__title'>
        I’m new, how to start?
      </h1>
      <p className='three-step__text'>
        You can watch this video later on the
      </p>
      <NavLink to='/' className='three-step__resources-page'>Resources page</NavLink>
      {/*<div className='three-step__video' onClick={() => setShowPopup(true)}>*/}
      {/*  <img src={videoPreview} alt='preview'/>*/}
      {/*</div>*/}
      <div className='three-step__btn'>
        <Button><NavLink to='/'>Go to mainpage <img src={arrow} alt='arrow'/></NavLink></Button>
      </div>
      {showPopup && <Popup setShowPopup={setShowPopup}/>}
    </div>
  );
};



