import React, {useState, useEffect} from 'react';
import {Checkbox, Button} from 'antd';
import ModalCard from '../../../utils/modal-card';
import './index.scss';

const options = ['New contact added', 'New feedback received', 'Newsletter']

const Settings = ({onPage}) => {
  const [isModal, setIsModal] = useState(false)
  useEffect(() => onPage('settings'), [])
  return (
    <div className='settings'>
      <div className='settings__label'>
        Notifications by e-mail:
      </div>
      <Checkbox.Group options={options}/>
      <div className='settings__btn'>
        <Button className='blue'>Save changes</Button>
      </div>
      {isModal && <ModalCard reason='сhanges saved' setModal={setIsModal}/>}
    </div>
  );
};

export default Settings;
