import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import {NavLink} from 'react-router-dom';
import cn from 'classnames';
import {setCurrentPage, CURRENT_PAGE} from '../../redux/reducers/page';
import {CaseInfo} from '../../utils/supports';
import {arrowDown, arrowUp} from '../../common/images/materials';
import {defaultCase, defaultCasePreview, defaultCasePreview2} from '../../common/images/case-challenge';
import {CaseClose, Feedback, LastStep, Solutions, YourSolution} from './Footer';
import './index.scss';

const op = {
  year: '2-digit',
  month: '2-digit',
  day: '2-digit'
}

const files = [{title: 'Скачки напряжения.pdf', size: '76 мб'}, {title: 'Скачки напряжения.pdf', size: '76 мб'}, {title: 'Скачки напряжения.pdf', size: '76 мб'}];
const text = 'Ваш клиент – FastCom Group, крупный оператор телекоммуникационных сетей и мобильной связи. Его главный офис находится в России. Недавно FastCom вышла на российский рынок банковского обслуживания физических лиц, создав FastCom Bank. В настоящее время компания рассматривает варианты быстрого наращивания базы клиентов, которые пользуются банковскими услугами на повседневной основе. Клиент предложил вашей рабочей группе проанализировать возможные варианты расширения клиентской базы.'
const Cases = ({imgTitle, setCurrentPage}) => {
  const [currentStep, setCurrentStep] = useState(1)
  const [isShow, setIsShow] = useState(false)
  const [stepDate, setStepDate] = useState(new Intl.DateTimeFormat('ru-RU', op).format(new Date()))
  const [allAppreciated, setAllAppreciated] = useState(false)
  const [noWorkToEvaluate, setNoWorkToEvaluate] = useState(false)

  useEffect(() => setCurrentPage(CURRENT_PAGE, 'Case-Challenge'), [])

  return (
    <>
      {/*<DefaultCase/>*/}
      <div className='cases'>
        <div className='cases__top'>
          <div className='cases__top-card'>
            <div className='cases__top-card__img'>
              <h5 className='cases__top-card__img-title'>{imgTitle || 'Natural Resourses'}</h5>
              <img src={currentStep === 2 ? defaultCasePreview2 : defaultCasePreview} alt='preview'/>
            </div>
            <div className='cases__top-card__info'>
              <h3 className='cases__top-card__info-subtitle'>Operational improvements</h3>
              <h2 className='cases__top-card__info-title'>Elite Furniture</h2>
            </div>
          </div>
        </div>
        <div className='cases__description flex-start-between'>
          <div className='cases__description-title' onClick={() => setIsShow(!isShow)}>
            Context
            <img className={isShow ? 'arrow-up' : 'arrow-down'} src={isShow ? arrowUp : arrowDown} alt='arrow'/>
          </div>
          {isShow && <div className='cases__description-main'>
            <CaseInfo text={text} files={files} title='Files'/>
          </div>}
        </div>
        <div className='cases__steps'>
          <Steps step={currentStep} setCurrentStep={setCurrentStep} allAppreciated={allAppreciated} noWorkToEvaluate={noWorkToEvaluate}/>
        </div>
      </div>
      <div className='cases__case-challenge'>
        <NavLink to='/case-challenge'>Past Case Challenges</NavLink>
      </div>
    </>
  );
};

export default connect(null, {setCurrentPage})(Cases);

const Steps = ({step, setCurrentStep, allAppreciated, noWorkToEvaluate}) => {
  return (
    <div className='steps'>
      {step in steps && steps[step]({setCurrentStep, allAppreciated, noWorkToEvaluate})}
    </div>
  )
}

const TitleStep = ({title, text, children}) => {
  return (
    <div className='title-step'>
      <h2 className='title-step__title'>{title}</h2>
      {text && <p className='title-step__text'>{text}</p>}
      {children && <div className='title-step__child'>{children}</div>}
    </div>
  )
}

const StepInfo = ({step, text}) =>
  <div className='step-info'>
    <div className='step-info__current-step'>Stage {step}/4</div>
    <div className='step-info__allotted-date'>{text ? text : '07.12.2020 — 04.01.2021'}</div>
    <div className='step-info__left-date'>Time remaining: <span>24 days 4 hours</span></div>
  </div>

const partners = [
  {name: 'Самойлова Павла', link: '', decision: ''},
  {name: 'Старцева Евгения Павловна', link: '', decision: ''},
  {name: 'Коржавов Александр Сергеевич', link: '', decision: ''},
  {name: 'Константинопольский Константин Константинович', link: '', decision: ''},
  {name: 'Шапотатьева Ивана', link: '', decision: ''},
]

const steps = {
  1: ({setCurrentStep}) =>
    <>
      <div className='step step-first'>
        <StepInfo step={1}/>
        <TitleStep title='Сase' text='Solve the case and submit your answer'/>
      </div>
      <YourSolution setCurrentStep={setCurrentStep}/>
    </>,
  2: ({setCurrentStep, allAppreciated, noWorkToEvaluate}) =>
    <>
      <div className={cn('step step-second', {'mb200': allAppreciated || noWorkToEvaluate})}>
        <StepInfo step={2}/>
        <TitleStep step={2} title={allAppreciated ? 'You have reviewed all case answers! Thank you for your time!' : noWorkToEvaluate ? '' : 'Peer Feedback'}
                   text={allAppreciated ? 'Now you can take a rest and wait for the next stage!' : noWorkToEvaluate ? '' : 'Review 5 other case solutions.'}
        />
      </div>
      {!(allAppreciated || noWorkToEvaluate) && <Solutions partners={partners} setCurrentStep={setCurrentStep}/>}
    </>,
  3: ({setCurrentStep}) =>
    <>
      <div className='step step-third'>
        <StepInfo step={3}/>
        <TitleStep title='Feedback Analysis' text='Analyze all the feedback you received from others. Was it helpful?'/>
      </div>
      <Feedback partners={partners} setCurrentStep={setCurrentStep}/>
    </>,
  4: ({setCurrentStep}) =>
    <>
      <div className='step step-fourth'>
        <StepInfo step={4} text='Join our Case Study webinar on 12 November at 7 PM (CET).'/>
        <TitleStep title='Case Study Webinar with a McKinsey consultant'>
          <NavLink to='#'>Webinar link</NavLink>
          <div className='title-step__child-password'>(Password:<span>2020academy</span>)</div>
        </TitleStep>
      </div>
      <LastStep setCurrentStep={setCurrentStep}/>
    </>,
  5: () =>
    <div className='step-five'>
      <CaseClose/>
    </div>
}

const DefaultCase = () =>
  <div className='default-case'>
    <div className="default-case__title title">
      <div className="title-top">Релаксируем и вдохновляемся</div>
      <div className="title-main">Набираемся сил перед следующим кейсом!</div>
    </div>
    <div className="default-case__img"><img src={defaultCase} alt='default case'/></div>
  </div>
