import React, {useState, useEffect} from 'react';
import {NavLink} from 'react-router-dom';
import {Button, Switch, Checkbox} from 'antd';
import {connect} from 'react-redux';
import {close} from "../../common/images/account";
import {down, up, add, arrow, avatar, report, open} from '../../common/images/partners';
import {setCurrentPage, CURRENT_PAGE} from '../../redux/reducers/page';
import {PartnersIcon} from '../../utils/icon/partners';
import ModalCard from "../../utils/modal-card";
import {SelectItem} from '../../utils/supports';
import './index.scss';

const selects = [
  {
    placeholder: 'Tags',
    options: [
      {id: 1,checked: false, option: 'I\'m preparing for a case interview'},
      {id: 2,checked: false, option: 'I want to participate in a case competition'},
      {id: 3,checked: false, option: 'I want to develop my problem solving skills'},
      {id: 4,checked: false, option: 'I want to expand my professional network'}]
  },
  {
    placeholder: 'Country',
    options: [
      {id: 1,checked: false, option: 'USA'},
      {id: 2,checked: false, option: 'Russia'},
      {id: 3,checked: false, option: 'Spain'},
      {id: 4,checked: false, option: 'Israel'},
      {id: 5,checked: false, option: 'Armenia'},
      {id: 6,checked: false, option: 'Italy'}
      ]
  },
  {
    placeholder: 'Language',
    options: [
      {id: 1,checked: false, option: 'English'},
      {id: 2,checked: false, option: 'Arabic'},
      {id: 3,checked: false, option: 'Turkish'},
      {id: 4,checked: false, option: 'Azeri'},
      {id: 5,checked: false, option: 'French'},
      {id: 6,checked: false, option: 'Russian'},
      {id: 7,checked: false, option: 'Kazakh'},
      {id: 8,checked: false, option: 'Portuguese'},
      {id: 9,checked: false, option: 'Afrikaans'},
      {id: 10,checked: false, option: 'Swahili'},
      {id: 11,checked: false, option: 'Spanish'},
      {id: 12,checked: false, option: 'Dutch'},
      {id: 13,checked: false, option: 'Italian'},
      {id: 14,checked: false, option: 'German'}
      ]
  },
  {
    placeholder: 'My graduation year',
    checked: false
  },
  {
    placeholder: 'Case Challenge participant',
    checked: false
  }
]

const defPartner = {
  id: 1,
  name: 'Marie-Thérèse Cassidy',
  avatar: avatar,
  lastActive: 'a few days ago',
  tags: ['Preparing for an interview', 'I am looking for a team to participate'],
  language: ['English', 'Russian', 'Spanish'],
  cells: [
    {title: 'I prefer to communicate by', text: ['Email']},
    {title: 'University', text: 'Saint Petersburg State University'},
    {title: 'My graduation year', text: '2008 год'},
    {
      title: 'About me',
      text: 'Angie Yuanmalai, Associate Creative Director at Huge, uncovers how user experience can change the way consumers and brands interact with each other. Huge is an experience design and digital marketing agency that provides services to Fortune 100 companies. They apply rapid prototyping, ongoing usability testing and iterative development to bridge the gap between the digital and physical experiences. Angie Yuanmalai will be talking about building and living a creative culture.'
    },
    {title: 'Language', text: ['English', 'Russian', 'Spanish']},
    {title: 'Country', text: 'USA'},
    {title: 'Tags', text: ['Preparing for an interview', 'I am looking for a team to participate']},
    {title: 'Case Challenge participant', text: 'Yes'}
  ]
}
const partner2 = {
  id: 2,
  name: 'Marie Cassidy',
  avatar: null,
  lastActive: 'a few days ago',
  tags: ['Preparing for an interview', 'I am looking for a team to participate'],
  language: ['English', 'Russian'],

  cells: [
    {title: 'I prefer to communicate by', text: ['Email']},
    {title: 'My graduation year', text: '2222 год'},
    {
      title: 'About me',
      text: 'Angie Yuanmalai, Huge is an experience design and digital marketing agency that provides services to Fortune 100 companies. They apply rapid prototyping, ongoing usability testing and iterative development to bridge the gap between the digital and physical experiences. Angie Yuanmalai will be talking about building and living a creative culture.'
    },
    {title: 'Language', text: ['English', 'Russian']},
    {title: 'Country', text: 'Russia Federation'},
    {title: 'Tags', text: ['Preparing for an interview', 'I am looking for a team to participate']},
    {title: 'Case Challenge participant', text: 'Yes'}
  ]
}

const defPartners = [defPartner, partner2]

const Buddies = ({width, setCurrentPage}) => {
  const [filters, setFilters] = useState({})
  const [selectFilter, setSelectFilter] = useState([])
  const [partner, setPartner] = useState(defPartner)
  const [modal, setModal] = useState(false)
  const [isShow, setIsShow] = useState(false)
  const [isShowSearch, setIsShowSearch] = useState(false)
  const [toggleFilter, setToggleFilter] = useState(selects)
  const [placeholder, setPlaceholder] = useState('')

  const onNextPartner = (pId) => {
    setPartner(defPartners[pId])
  }
  const onChangeFilter = (p, {option, checked}) => {
    setFilters({...filters, [option]: checked})
    setSelectFilter(s => [...new Set([...s, option])])
    setPlaceholder(p)
  }

  useEffect(() => {
    setSelectFilter(s => s.filter(f => filters[f]))
    setIsShowSearch(false)
    onFilterForSearchPartner(placeholder)
  }, [filters])

  const dropSelectFilter = (option) => selectFilter.forEach(f => f === option && setFilters({...filters, [option]: false}))

  const clearFilter = () => setFilters([])

  const showClearAll = () => {
    for (let i in filters) {
      if (filters[i]) return true
    }
    return false
  }
  const onFilterForSearchPartner = (p) => {
    setToggleFilter(prev => prev.map(({placeholder, checked, options}) => placeholder === p
      ? {placeholder, checked, options: options.map(({checked, option}) => ({checked: filters[option] || checked, option}))}
      : {placeholder, checked, options}
      )
    )
    // console.log(selects.map(({placeholder, checked, options}) => {
    //   return checked !== undefined
    //     ? ({placeholder: placeholder.toLowerCase().replace(/ /g, '-'), checked})
    //     : ({placeholder: placeholder.toLowerCase().replace(/ /g, '-'), options})
    // }))

  }
  useEffect(() => setCurrentPage(CURRENT_PAGE, 'Buddies'), [])
  return (
    <section className='partners'>
      <div className='partners__top'>
        {width > 991 && <div className='partners__top-icon'>
          <PartnersIcon/>
        </div>}
        <div className='partners__top-headers'>
          <h1 className='partners__top-headers__title'>
            Buddies
            {width <= 991 && <div className='partners__top-icon'>
              <PartnersIcon/>
            </div>}
          </h1>
          <h2 className='partners__top-headers__subtitle'>Find a buddy to learn and practice cases together.</h2>
        </div>
      </div>

      <div className='partners__main'>
        <div className='partners__main-wrapper'>
          <div className='partners__main-wrapper__filter'>
            <div className='partners__main-wrapper__top'>
              <div className='partners__main-wrapper__top-title' onClick={() => setIsShow(!isShow)}>
                <span>Filter</span>
                {width <= 991 && (isShow
                  ? <img src={close} alt='open filters'/>
                  : <img src={open} alt='open filters'/>)}
              </div>
              {width > 991 && showClearAll() &&
              <div className='partners__main-wrapper__top-clear' onClick={clearFilter}>
                Clear all
              </div>}
            </div>

            {width > 991 && selects.map(({placeholder, options}) =>
              <Filter key={placeholder} placeholder={placeholder}
                      checkList={filters}
                      options={options} onChange={onChangeFilter}/>)}

            {isShow && selects.map(({placeholder, options}) =>
              <Filter key={placeholder} placeholder={placeholder}
                      checkList={filters}
                      options={options} onChange={onChangeFilter}/>)}

            {width <= 991 && isShow &&
            <div className='partners__main-wrapper__top-btn'>
              <Button onClick={clearFilter}>Clear all</Button>
              <Button className='blue' onClick={() => setIsShow(!isShow)}>
                Apply
              </Button>
            </div>
            }
          </div>
          <div className="flex-center">
            <div className='partners__main-wrapper__left'>
              <div className='partners__main-wrapper__left-filters'>
                {!!selectFilter.length && selectFilter.map(option => <SelectItem key={option} option={option} dropSelect={dropSelectFilter}/>)}
              </div>
              <div className='partners__main-wrapper__left-card'>
                <Card canUserSearchPartner={true} isShowSearch={isShowSearch} setIsShowSearch={setIsShowSearch} partner={partner} onNextPartner={onNextPartner}
                      setModal={setModal}/>
              </div>
            </div>
          </div>
        </div>
      </div>
      {modal && <ModalCard reason='report' setModal={setModal}/>}
    </section>
  );
};

export default connect(({windowSize, user}) => ({width: windowSize.width, user}), {setCurrentPage})(Buddies);

const Filter = ({placeholder, options = [], onChange, checkList}) => {
  const ccp = 'Case Challenge participant';
  const gy = 'My graduation year';
  const [show, setShow] = useState(false);

  const onCheckboxChange = ({checked, option}) => {
    onChange(placeholder, {option, checked: !checked})
  }

  return (
    <div className='filter'>
      <div className='filter__placeholder' onClick={() => setShow(!show)}>
        {placeholder}
        {!(placeholder === ccp || placeholder === gy) &&
        <div className='filter__arrow'>
          <img className={show ? 'arrow-down' : 'arrow-up'} src={show ? up : down} alt='arrow'/>
        </div>}
        {placeholder === ccp && <Switch/>}
        {placeholder === gy && <Switch/>}
      </div>
      {show && !(placeholder === ccp || placeholder === gy) &&
      <div className='filter__show'>
        {!!options.length && options.map(({checked, option}) =>
          <Checkbox key={option} checked={!!Object.keys(checkList).length ? checkList[option] : checked}
                    onChange={() => onCheckboxChange({checked: !!Object.keys(checkList).length ? checkList[option] : checked, option})}>
            {option}
          </Checkbox>
        )}
      </div>
      }
    </div>
  )
};

const Card = ({partner, canUserSearchPartner, setModal, onNextPartner, isShowSearch, setIsShowSearch}) => {

  return (
    <div className='card'>
      {
        !canUserSearchPartner
          ? <CardCanSearchPartner/>
          : !isShowSearch
          ? <CardSearchPartner setIsShowSearch={setIsShowSearch}/>
          : partner
            ? <CardPartner {...partner} setModal={setModal} onNextPartner={onNextPartner}/>
            : <CardNotFoundPartner/>
      }
    </div>
  )
};

const CardSearchPartner = ({setIsShowSearch}) =>
  <div className='card-search-partner padding-card'>
    <p>By using search filters you can
      organize the list according to your
      needs.</p>
    <Button className='blue' shape='round' size='large' onClick={() => setIsShowSearch(true)}>Find a buddy</Button>
  </div>

const CardPartner = ({id, name, avatar, lastActive, cells, onNextPartner, setModal}) => {
  return (
    <div className='card-partner padding-cart-partner'>
      <div className='card-partner__avatar'>
        {avatar
          ? <img src={avatar} alt='avatar'/>
          : <div className='card-partner__avatar-img'>
            MC
          </div>
        }

        <div className='report' onClick={() => setModal('report')}>
          Report
          <img src={report} alt='report'/>
        </div>
      </div>
      <div className='card-partner__info'>
        <div className='card-partner__info-name'>
          {name}
          <div className='last-active'>
            Last active:<span>{lastActive}</span>
          </div>
        </div>
        <div className='card-partner__info-cells'>
          {cells && cells.map(({title, text}) => <CardPartnerCell key={text} title={title} text={text}/>)}
        </div>
        <div className='card-partner__info-btn'>
          <Button className='blue'>
            Add contact
            <img src={add} alt='add'/>
          </Button>
          <Button onClick={() => onNextPartner(id)}>
            Next
            <img src={arrow} alt='arrow'/>
          </Button>
        </div>
      </div>
    </div>
  )
}

const CardPartnerCell = ({title, text}) => {
  return (
    <div className='card-partner-cell'>
      <h5 className='card-partner-cell__title'>
        {title}
      </h5>
      <p className='card-partner-cell__text'>
        {title !== 'Tags' && (Array.isArray(text)
          ? text.join(', ')
          : <span className={title === 'Case Challenge participant' ? 'participant' : undefined}>{text}</span>)}
        {title === 'Tags' && text.map(t => <div key={t} className='tags'>{t}</div>)}
      </p>
    </div>
  )
}

const CardCanSearchPartner = () =>
  <div className='card-can-search-partner padding-card'>
    <h3>Поиск недоступен</h3>
    <p>Чтобы начать поиск партнеров, нужно чтобы ваш профиль тоже был открыт для поиска другими участниками проекта. Открыть поиск можно в настройках профиля.</p>
    <Button className='blue' shape='round' size='large'><NavLink to='/account/profile'>Перейти в профиль</NavLink></Button>
  </div>

const CardNotFoundPartner = () =>
  <p className='card-not-found-partner padding-card'>
    Seems like there is no one that
    matches your criteria. Please
    change filters and try again!
  </p>
