import React from 'react';
import cn from 'classnames';
import './index.scss';

const Avatar = ({src, setSrc, icon, initials}) => {
  const readURL = (e) => {
    if (e.target.files && e.target.files[0]) {
      let reader = new FileReader();
      reader.onload = function (e) {
        setSrc(e.target.result);
      }
      reader.readAsDataURL(e.target.files[0]);
    }
  }
  return (
    <div className='avatar' style={!icon ? {padding: '0'} : {}}>
      {icon
        ? <>
          <label className='upload-button' htmlFor='avatar-upload'>
            {src
              ? <img className='profile-pic' src={src} alt='avatar'/>
              : <div className='initials'><div className='initials__inner'>{initials}</div></div>
            }
            <div className={cn({'add-photo': !!icon})} style={icon && {backgroundImage: `url(${icon})`}}/>
          </label>
          <input className='file-upload' id='avatar-upload' type='file' accept='image/*' onChange={readURL}/>
        </>
        : <>
          {src
            ? <img className='profile-pic' src={src} alt='avatar'/>
            : <div className='initials'><div className='initials__inner'>{initials}</div></div>
          }
        </>
      }
    </div>
  );
};

export default Avatar;
