import React, {useState} from 'react';
import {Input, Button} from 'antd';
import cn from 'classnames';
import {close} from '../../common/images/account';
import './index.scss';

const modal = [
  {
    reason: 'delete',
    title: 'Are you sure you want to delete the contact?',
    text: 'Your contact information will also be removed from your partner account.',
    btn1: 'Delete',
    btn2: 'No, I’ve change my mind'
  },
  {reason: 'cancel', title: 'Are you sure you want to withdraw this request?', btn1: 'Yes', btn2: 'No I’ve changed my mind'},
  {
    reason: 'no add',
    title: 'Are you sure you don’t want to add a buddy?',
    text: 'The partner will not see your contact details either',
    btn1: 'Yes',
    btn2: 'No, I’ve changed my mind'
  },
  {
    reason: 'no register',
    title: 'To get access to the full functionality of the platform, you need to complete a full registration.',
    btn1: 'Register',
    btn2: 'Cancel'
  },
  {
    reason: 'no register second step',
    title: 'It seems you haven\'t completed registration!',
    text: 'To get access to the full functionality of the platform, you need to complete the registration.',
    btn1: 'Finish registration',
    btn2: 'Later'
  },
  {
    reason: 'сhanges saved',
    title: 'Changes saved',
    btn1: 'Cancel'
  }
]

const ModalCard = ({reason, partners, partnerId, setPartners, setModal}) => {
  const onClick = (reason, btn) => {
    (reason === 'delete' || reason === 'cancel' || reason === 'no add') && btn === 1 && setPartners(partners.filter(p => p.id !== partnerId))
    reason === 'no add' && btn === 2 && setPartners(partners.map(p => p.id === partnerId ? {...p, status: 'my contact'} : p))
    setModal(null)
  }
  console.log(reason)
  return (
    <div className={cn('modal-card', {'dn': !reason})}>
      <div className='modal-card__modal'>
        <div className='modal-card__close' onClick={() => setModal(null)}>
          <img src={close} alt='close'/>
        </div>
        {reason === 'report'
          ? <Report setModal={setModal}/>
          : modal.map(m => m.reason === reason && <ContactChange {...m} onClick={onClick}/>)}
      </div>
    </div>
  )
}

export default ModalCard;

const ContactChange = ({title, text, reason, btn1, btn2, onClick}) => {
  return (
    <div className='contact-change'>
      <div className='contact-change__title'>
        {title}
      </div>
      <div className='contact-change__text'>
        {text}
      </div>
      <Button className='blue' onClick={() => onClick(reason, 1)}>
        {btn1}
      </Button>
      <Button onClick={() => onClick(reason, 2)}>
        {btn2}
      </Button>
    </div>
  )
}

const Report = ({setModal}) => {
  const [text, setText] = useState('')
  const [isSent, setIsSent] = useState(false)
  return (
    <div className='report'>
      {!isSent ?
        <>
          <div className='report__title'>Report</div>
          <p className='report__text'>Describe the cause of the complaint,
            e.g. vulgar language in profile description</p>
          <Input.TextArea placeholder='Text of complaint' onChange={setText}/>
          <Button className='blue' onClick={() => setIsSent(true)}>Send</Button>
        </>
        : <>
          <div className='report__title'>Сообщение отправлено</div>
          <p className='report__text'>Ваше сообщение отправлено команде Case Academy. Мы рассмотрим заявку и предпримем необходимые меры. Спасибо за бдительность.</p>
          <Button onClick={() => setModal(null)}>Закрыть</Button>
        </>
      }
    </div>
  )
}


export const Modal = ({showModal, setShowModal, children}) => {

  return (
    <div className={cn('modal-card', {'dn': !showModal})}>
      <div className='modal-card__modal'>
        <div className='modal-card__close' onClick={() => setShowModal(false)}>
          <img src={close} alt='close'/>
        </div>
        <div className='modal-card__child'>
          {children}
        </div>
      </div>
    </div>
  )
}
