import React, {useState} from 'react';
import {Button} from 'antd';
import {connect} from "react-redux";
import {CardPartnerCell} from '../PrivateAccount/Contacts';
import Avatar from '../../utils/avatar';
import ModalCard from '../../utils/modal-card';
import {add, report} from '../../common/images/partners';
import './index.scss';

const partner = {
  firstName: 'Marie-Thérèse',
  lastName: 'Cassidy',
  avatar: null,
  status: 'I was added',
  lastActive: 'a few days ago',
  cells: [
    {emailPhone: {email: 'dophuonganh1010@gmail.com', phone: '+79651570792'}},
    {title: 'I prefer to connect by', text: ['Email']},
    {title: 'University', text: 'Санкт-Петербургский государственный университет'},
    {title: 'My graduation year', text: '2008 год'},
    {
      title: 'About me',
      text: 'Angie Yuanmalai, Associate Creative Director at Huge uncovers how user experience can change the way consumers and brands interact with each other. Huge is an experience design and digital marketing agency that provides services to Fortune 100 companies. They apply rapid prototyping, ongoing usability testing and iterative development to bridge the gap between the digital and physical experiences. Angie Yuanmalai will be talking about building and living a creative culture.'
    },
    {title: 'Language', text: ['English', 'Russian', 'Spanish']},
    {title: 'Country', text: 'Russia Federation'},
    {title: 'Tags', text: ['Preparing for an interview', 'I am looking for a team to participate']},
    {title: 'Case Challenge participant', text: 'Yes'}
  ]
}

const User = ({width}) => {
  const [modal, setModal] = useState(null)
  return (
    <div className='user-profile'>
      <Profile {...partner} setModal={setModal} width={width}/>
      {modal && <ModalCard reason={modal} setModal={setModal}/>}
    </div>
  );
};

export default connect(({windowSize}) => ({width: windowSize.width}))(User);

const Profile = ({firstName, lastName, avatar, lastActive, cells, setModal, width}) => {
  return (
    <div className='card-partner'>
      <div className='card-partner__avatar'>
        <div className='account__top-avatar user-profile-avatar'>
          <Avatar src={avatar} initials='MTC'/>
        </div>
        {width <= 479 && <div className='card-partner__info-name'>
          <span className='card-partner__info-name-first'>{firstName}</span>
          <span className='card-partner__info-name-last'>{lastName}</span>
        </div>}
      </div>
      <div className='card-partner__info'>
        <div className='card-partner__info-wrapper'>
          {width > 479 && <div className='card-partner__info-name'>
            <span className='card-partner__info-name-first'>{firstName}</span>
            <span className='card-partner__info-name-last'>{lastName}</span>
          </div>}
          <div className='last-active'>
            Last active:<span>{lastActive}</span>
          </div>
          {cells && cells.map(({title, text, emailPhone}, i) => <CardPartnerCell key={title + i} emailPhone={emailPhone} title={title} text={text}/>)}
        </div>
        <Button className='blue'>Add contact <img src={add} alt='add contact'/></Button>
        <span className='card-partner__report' onClick={() => setModal('report')}>
          Report <img src={report} alt='report'/>
        </span>
      </div>
    </div>
  )
}
