import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import {Button} from 'antd';
import {NavLink} from 'react-router-dom';
import {card1, card2, card3, AlexeiEvdokimov, reload} from '../../common/images/main';
import {big, middle, small} from '../../common/images/main/paralax';
import {setCurrentPage, CURRENT_PAGE} from '../../redux/reducers/page';
import {setUserIsAuth, USER_IS_AUTH} from '../../redux/reducers/user';
import {Modal} from '../../utils/modal-card';
import './index.scss';

const Main = ({isAuth, setCurrentPage, setUserIsAuth, width}) => {
  const [showModal, setShowModal] = useState(false)
  const [scroll, setScroll] = useState(null)

  const onScroll = () => {
    setScroll(window.scrollY)
  }

  useEffect(() => {
    setCurrentPage(CURRENT_PAGE, 'Main')
    window.addEventListener('scroll', onScroll)

    return () => {
      window.removeEventListener('scroll', onScroll)
    }
  }, [])

  return (
    <div className='main'>
      <section className='welcome container'>
        <div className='welcome__title title'>
          <h2 className='title-top'>Case Academy</h2>
          <h3 className='title-main'>
            Join the biggest case community!<br/>
            We welcome everyone from complete newbies to highly experience casers!
          </h3>

        </div>
        <div className='welcome__cards'>
          <div className='welcome__cards-card'>
            <img className='welcome__cards-card__img' src={card1} alt='card'/>
            <div className='welcome__cards-card__title'>Learn</div>
            <p className='welcome__cards-card__text'>Solve cases, receive feedback, get better</p>
          </div>
          <div className='welcome__cards-card'>
            <img className='welcome__cards-card__img' src={card2} alt='card'/>
            <div className='welcome__cards-card__title'>Connect</div>
            <p className='welcome__cards-card__text'>Connect with others and extend your professional network</p>
          </div>
          <div className='welcome__cards-card'>
            <img className='welcome__cards-card__img' src={card3} alt='card'/>
            <div className='welcome__cards-card__title'>Practice</div>
            <p className='welcome__cards-card__text'>Solve cases on your own or with a team of buddies</p>
          </div>
        </div>
        <div className='welcome__btn flex-just-center'>
         <NavLink to='/case-challenge'><button className='btn'>Let's start</button></NavLink>
        </div>
      </section>

      <section className='partners bg-img'>
        <div className='partners__wrapper'>
          <img className='partners__img' src={big} alt='big-circle' style={{top: `${(scroll - (width < 778 ? 1300 : 2100)) * 0.5}px`}}/>
          <img className='partners__img' src={middle} alt='middle-circle' style={{top: `${(scroll - (width < 778 ? 1300 : 2100)) * 0.5}px`, left: `${(scroll - (width > 778 ? 1000 : 2100)) * 0.05}px`}}/>
          <img className='partners__img' src={small} alt='small-circle' style={{top: `${(scroll - (width < 778 ? 1300 : 2100)) * 0.5}px`}}/>
        </div>
        <div className='partners__inner'>
          <div className='partners__title title'>
            <h2 className='title-top'>Case Buddies</h2>
            <h3 className='title-main'>
              Extend your network and practice cases with others. Ace your consulting case interview!
            </h3>
          </div>
          <div className='partners__btn flex-just-center'>
            <NavLink to='/buddies'>
              <button className='btn'>Find buddies</button>
            </NavLink>
          </div>
        </div>
      </section>

      <section className='challenge container'>
        <div className='challenge__title title'>
          <h2 className='title-top'>Case Challenge</h2>
          <h3 className='title-main'>
            4 Steps to Ace Consulting Case Interview
          </h3>
        </div>
        <div className='challenge__steps'>
          <div className='challenge__steps-inner'>
            <div className='challenge__steps-step'>
              <div className='challenge__steps-step__num'>1</div>
              <p className='challenge__steps-step__text'>Solve Cases</p>
            </div>
            <div className='challenge__steps-line'/>
          </div>

          <div className='challenge__steps-inner' style={{marginLeft: '40px'}}>
            <div className='challenge__steps-step'>
              <div className='challenge__steps-step__num' style={{width: '240px'}}>2</div>
              <p className='challenge__steps-step__text'>Peer Feedback</p>
            </div>
            <div className='challenge__steps-line line2'/>
          </div>

          <div className='challenge__steps-inner' style={{marginLeft: '30px'}}>
            <div className='challenge__steps-step'>
              <div className='challenge__steps-step__num'>3</div>
              <p className='challenge__steps-step__text'>Feedback Analysis</p>
            </div>
            <div className='challenge__steps-line line3'/>
          </div>

          <div className='challenge__steps-inner'>
            <div className='challenge__steps-step'>
              <div className='challenge__steps-step__num'>4</div>
              <p className='challenge__steps-step__text'>Case Study Webinar with a McKinsey Consultant</p>
            </div>
          </div>

        </div>
        <div className='challenge__btn flex-just-center'>
         <NavLink to='/case-challenge'><button className='btn'>Participate</button></NavLink>
        </div>
      </section>

      <section className='users-countries bg-img container'>
        <div className='users-countries__num'>1500+</div>
        <p className='users-countries__text'>users from 40 countries</p>
      </section>

      {/*<section className='feedback-main container'>*/}
      {/*  <div className='feedback-main__img'>*/}
      {/*    <img src={AlexeiEvdokimov} alt='Alexei Evdokimov'/>*/}
      {/*  </div>*/}
      {/*  <div className='feedback-main__inner'>*/}
      {/*    <div className='feedback-main__inner-top'>*/}
      {/*      <div className='feedback-main__inner-top__info'>*/}
      {/*        <div className='feedback-main__inner-top__info-name'>Alexei Evdokimov</div>*/}
      {/*        <div className='feedback-main__inner-top__info-job'>co-founder tbdbo agency</div>*/}
      {/*      </div>*/}
      {/*      <div className='feedback-main__inner-top__btn'>*/}
      {/*        <button className='btn'>*/}
      {/*          <img src={reload} alt='reload'/>*/}
      {/*        </button>*/}
      {/*      </div>*/}
      {/*    </div>*/}
      {/*    <div className='feedback-main__inner-line'/>*/}
      {/*    <p className='feedback-main__inner-text'>*/}
      {/*      Really enjoyed the variety and calibre of the speakers. Needless to say that the organisation was spotless. Would recommend to anyone who*/}
      {/*      is working or interested in innovation.*/}
      {/*    </p>*/}
      {/*    <div className='feedback-main__inner-btn'>*/}
      {/*      {isAuth*/}
      {/*        ? <button className='btn' onClick={() => setShowModal(true)}>Registration</button>*/}
      {/*        : <NavLink to='/register'>*/}
      {/*          <button className='btn'>Registration</button>*/}
      {/*        </NavLink>*/}
      {/*      }*/}
      {/*    </div>*/}
      {/*  </div>*/}
      {/*</section>*/}

      {showModal &&
      <Modal showModal={showModal} setShowModal={setShowModal}>
        <p className='modal-text'>Вы уже авторизованы, если требуется создание нового аккаунта, нужно выйти из аккаунта и создать новую учетную запись</p>
        <Button className='blue' onClick={() => setUserIsAuth(USER_IS_AUTH, false)}><NavLink to='/register'>Sign out</NavLink></Button>
      </Modal>
      }
    </div>
  );
};

const mstp = ({page, user, windowSize}) => ({...page, isAuth: user.isAuth, width: windowSize.width})

export default connect(mstp, {setCurrentPage, setUserIsAuth})(Main)
