import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import cn from 'classnames';
import {Switch, Route, NavLink} from 'react-router-dom';
import Avatar from '../../utils/avatar';
import {SearchAccess} from '../../utils/supports';
import Contacts from './Contacts';
import MyCases from './MyCases';
import Profile from './Profile';
import Settings from './Settings';
import {setCurrentPage, CURRENT_PAGE} from '../../redux/reducers/page';
import {pen, camera} from '../../common/images/account';
import './index.scss';

const PrivateAccount = ({page, setCurrentPage, language}) => {
  const [currentShowPage, setCurrentShowPage] = useState('contacts')
  const [src, setSrc] = useState(null)
  const [width, setWidth] = useState(document.documentElement.clientWidth)

  const onResize = e => setWidth(e.target.innerWidth)
  useEffect(() => {
    setCurrentPage(CURRENT_PAGE, 'PrivateAccount')
    setWidth(document.documentElement.clientWidth)
    window.addEventListener('resize', onResize)
    return () => {
      window.removeEventListener('resize', onResize)
    }
  }, [])

  return (
    <div className='account'>
      <div className='account__top'>
        <div className='flex-align-center'>
          <div className='account__top-avatar'>
            <Avatar src={src} setSrc={setSrc} initials='NMR' icon={src ? pen : camera}/>
          </div>
          <div className='account__top-name'>
            Nepomuceno María Remedios
          </div>
        </div>
        {width > 778 && currentShowPage === 'profile' && <div className='account__top-switch'><SearchAccess/></div>}
      </div>
      <div className='account__main' style={page === 'profile' ? {marginTop: '63px'} : {marginTop: '110px'}}>
        <div className='account__main-menu'>
          <ul className='account__main-menu__lists'>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'contacts' === currentShowPage})}><NavLink to='/account/contacts'>Contacts</NavLink></li>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'profile' === currentShowPage})}><NavLink to='/account/profile'>Profile</NavLink></li>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'my-cases' === currentShowPage})}><NavLink to='/account/my-cases'>My&nbsp;cases</NavLink></li>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'settings' === currentShowPage})}><NavLink to='/account/settings'>Settings</NavLink></li>
          </ul>
          {width <= 778 && currentShowPage === 'profile' && <div className='account__top-switch'><SearchAccess/></div>}
        </div>
        <div className='account__main-wrapper'>
          <Switch>
            <Route path='/account/contacts' exact render={() => <Contacts width={width} onPage={setCurrentShowPage}/>}/>
            <Route path='/account/profile' exact render={() => <Profile onPage={setCurrentShowPage} language={language}/>}/>
            <Route path='/account/my-cases' exact render={() => <MyCases onPage={setCurrentShowPage}/>}/>
            <Route path='/account/settings' exact render={() => <Settings onPage={setCurrentShowPage}/>}/>
          </Switch>
        </div>
      </div>
    </div>
  );
};

export default connect(({page}) => ({...page}), {setCurrentPage})(PrivateAccount);
