import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import cn from 'classnames';
import {setCurrentPage} from '../../../redux/reducers/page';
import Layout from '../../../utils/layout/service';
import {FirstStep} from './first-step';
import {SecondStep} from './second-step';
import {ThreeStep} from './three-step';

const Register = ({page, setCurrentPage, isMenuShow, language, setIsMenuShow, width}) => {
  const [step, setStep] = useState(1)
  const [finishFirstStep, setFinishFirstStep] = useState(null)
  const [finishSecondStep, setFinishSecondStep] = useState(null)

  useEffect(() => {

  }, [finishFirstStep, finishSecondStep])
  return (
    <Layout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
      <div className='register'>
        {step === 1
          ? <>
            <h1 className='title'>{page}</h1>
            <div className='steps'>
              {width > 680 &&<div className={cn('step', {'current-step': step === 2})}/>}
            </div>
            <FirstStep setCurrentPage={setCurrentPage} setStep={setStep} setFinish={setFinishFirstStep}/>
          </>
          : step === 2
            ? <SecondStep step={step} setStep={setStep} setFinish={setFinishSecondStep} width={width} language={language}
                          initials={finishFirstStep.firstName[0] + finishFirstStep.lastName[0]}
                          name={finishFirstStep.firstName + ' ' + finishFirstStep.lastName}/>
            : <ThreeStep/>
        }
      </div>
    </Layout>
  )
};

export default connect(({page, windowSize}) => ({...page, width: windowSize.width}), {setCurrentPage})(Register);
