import React, {useEffect, useState, useRef} from 'react';
import {Form, Input, Select, Checkbox, Button} from 'antd';
import {NavLink} from 'react-router-dom';
import {phoneBritain} from '../../../common/images/account';
import {CURRENT_PAGE} from '../../../redux/reducers/page';
import {api} from '../../../utils/api';
import {NoVisible, Visible} from '../../../utils/icon/eye';

const {Option} = Select;

export const FirstStep = ({setCurrentPage, setStep, setFinish}) => {
  const [isVisible, setIsVisible] = useState(false);
  const [isVisible2, setIsVisible2] = useState(false);
  const [form] = Form.useForm();

  const onFinish = values => {
    try {
      const res = api.createUser(values)

    } catch (e) {
    }
    setFinish(values)
    setStep(2)
  };

  const passwordRef = useRef(null)
  const passwordRef2 = useRef(null)

  useEffect(() => {
    isVisible
      ? passwordRef.current.input.type = 'text'
      : passwordRef.current.input.type = 'password'
    isVisible2
      ? passwordRef2.current.input.type = 'text'
      : passwordRef2.current.input.type = 'password'
  }, [isVisible, isVisible2])

  const prefixSelector = (
    <Form.Item name='prefix' noStyle>
      <Select style={{width: 70}}>
        <Option value='+7'><img src={phoneBritain} alt='phone country'/> +7</Option>
        <Option value='86'><img src={phoneBritain} alt='phone country'/> +86</Option>
        <Option value='87'><img src={phoneBritain} alt='phone country'/> +87</Option>
      </Select>
    </Form.Item>
  );

  useEffect(() => {
    setCurrentPage(CURRENT_PAGE, 'Registration')
  }, [])

  return (
    <Form
      form={form}
      name='register'
      onFinish={onFinish}
      initialValues={{
        prefix: '+7',
      }}
      scrollToFirstError>

      <Form.Item
        name='firstName'
        label='First name'
        rules={[{required: true, message: 'Please input your First name!', whitespace: true},
          {
            validator: (_, value) => {
              if (/^[A-Za-z0-9]+$/g.test(value)) {
                return Promise.resolve()
              }
              return Promise.reject('Use only Latin letters!')
            }
          }]}>
        <Input/>
      </Form.Item>
      <Form.Item
        name='lastName'
        label='Last name'
        rules={[{required: true, message: 'Please input your Last name!', whitespace: true},
          {
            validator: (_, value) => {
              if (/^[A-Za-z0-9]+$/g.test(value)) {
                return Promise.resolve()
              }
              return Promise.reject('Use only Latin letters!')
            }
          }]}>
        <Input/>
      </Form.Item>

      <Form.Item
        name='password'
        label='Password'
        rules={[
          {required: true, message: 'Please input your password!'},
          {
            validator: (_, value) => {
              if (/(?=.*[0-9])(?=.*[!@#$%+^&{}_*])(?=.*[a-z])(?=.*[A-Z])[0-9!@#$+%{}_^&*a-zA-Z]{6,}/g.test(value)) {
                return Promise.resolve()
              }
              return Promise.reject('The password must contain at least 8 characters (Latin letters, numbers and at least one character: ! * + % @ ^ & { } _ #)')
            }
          }
        ]}>
        <Input.Password ref={passwordRef}
                        iconRender={_ => (isVisible
                          ? <Visible isVisible={isVisible} setIsVisible={setIsVisible}/>
                          : <NoVisible isVisible={isVisible} setIsVisible={setIsVisible}/>)}/>
      </Form.Item>

      <Form.Item
        name='confirm'
        label='Repeat password'
        dependencies={['password']}
        rules={[
          {
            required: true,
            message: 'Please confirm your password!',
          },
          ({getFieldValue}) => ({
            validator(rule, value) {
              if (!value || getFieldValue('password') === value) {
                return Promise.resolve();
              }
              return Promise.reject('Password doesn’t match');
            },
          }),
        ]}>
        <Input.Password ref={passwordRef2}
                        iconRender={_ => (isVisible2
                          ? <Visible isVisible={isVisible2} setIsVisible={setIsVisible2}/>
                          : <NoVisible isVisible={isVisible2} setIsVisible={setIsVisible2}/>)}
        />
      </Form.Item>

      <Form.Item
        name='country'
        label='Country'
        rules={[{required: true, message: 'Please select your country!'}]}>
        <Select className='ant-select-customize-input' onChange={null} placeholder='Please select a country'>
          <Select.Option value='usa'>USA</Select.Option>
        </Select>
      </Form.Item>

      <Form.Item
        name='email'
        label='E-mail'
        rules={[
          {type: 'email', message: 'The input is not valid E-mail!'},
          {required: true, message: 'Please input your E-mail!'}
        ]}>
        <Input/>
      </Form.Item>

      <Form.Item
        name='phone'
        label='Phone Number'
        rules={[{required: true, message: 'Please input your phone number!'},
          {
            validator: (_, value) => {
              if (/^[0-9]+$/g.test(value)) {
                return Promise.resolve()
              }
              return Promise.reject('Please enter only the phone number!')
            }
          }
        ]}>
        <Input className='input-phone' addonBefore={prefixSelector} maxLength={13} style={{width: '100%'}}/>
      </Form.Item>

      <div className='mt20'>
        <Form.Item
          className='border-none'
          name='agreement'
          valuePropName='checked'
          rules={[{validator: (_, value) => value ? Promise.resolve() : Promise.reject('Should accept agreement')}]}>
          <Checkbox>
            I have read and agree to the <NavLink to='/terms-of-use'>Terms of Use</NavLink>,
            &nbsp;<NavLink to='/rules-of-platform'>Rules of Platform</NavLink>
            &nbsp;and <NavLink to='/privacy-policy'>Privacy Policy.</NavLink>
          </Checkbox>
        </Form.Item>

        <Form.Item
          className='border-none'
          name='personal data'
          valuePropName='checked'
          rules={[{validator: (_, value) => value ? Promise.resolve() : Promise.reject('Should accept agreement')}]}>
          <Checkbox>
            I give my consent to the processing of my personal data.
          </Checkbox>
        </Form.Item>
      </div>
      <Form.Item className='border-none'>
        <Button className='blue mt20 mb22' htmlType='submit' shape='round'>
          Sign up
        </Button>
      </Form.Item>
      <div className='auth-link'>
        <NavLink to='/login' className='auth-arrow-icon'>Already have an account? Log in</NavLink>
      </div>
    </Form>
  );
};
