import React, {useEffect, useState} from 'react';
import {Button, Checkbox, Form, Input, Select} from 'antd';
import {connect} from 'react-redux';
import {api} from '../../../utils/api';
import ModalCard from '../../../utils/modal-card';
import './index.scss';


const Profile = ({onPage, id, language}) => {
  const [communication, setCommunication] = useState({})
  const [isModal, setIsModal] = useState(false)
  const [form] = Form.useForm();
  useEffect(() => onPage('profile'), [])

  const onFinish = async (values) => {
    const res = await api.profileUpdate({...values, communication, id})
    setIsModal(true)
  };

  return (
    <div className='profile'>
      <Form
        form={form}
        name='profile'
        onFinish={onFinish}
        initialValues={null}
        scrollToFirstError>

        <Form.Item
          name='firstName'
          label='First name'
          rules={[{required: true, message: 'Please input your First name!', whitespace: true},
          {
            validator: (_, value) => {
              if (/^[A-Za-z0-9]+$/g.test(value)) {
                return Promise.resolve()
              }
              return Promise.reject('Use only Latin letters!')
            }
          }]}>
          <Input/>
        </Form.Item>
        <Form.Item
          name='lastName'
          label='Last name'
          rules={[{required: true, message: 'Please input your Last name!', whitespace: true},
          {
            validator: (_, value) => {
              if (/^[A-Za-z0-9]+$/g.test(value)) {
                return Promise.resolve()
              }
              return Promise.reject('Use only Latin letters!')
            }
          }]}>
          <Input/>
        </Form.Item>

        <Form.Item name='university' label='University'>
          <Input/>
        </Form.Item>
        <Form.Item name='faculty' label='Faculty'>
          <Input/>
        </Form.Item>
        <Form.Item name='graduation-year' label='Graduation year'>
          <Select className='ant-select-customize-input' placeholder='Please select a graduation year'>
            <Select.Option value='2009'>2009</Select.Option>
            <Select.Option value='2008'>2008</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item name='country' label='Country'>
          <Select className='ant-select-customize-input' placeholder='Please select a country'>
            <Select.Option value='usa'>USA</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item
          name='language'
          label='Language'>
          <Select className='ant-select-customize-input' placeholder='Please select a language'>
            {language.map(l => <Select.Option value={l.toLowerCase()}>{l}</Select.Option>)}
          </Select>
        </Form.Item>
        <Form.Item name='about-me' label='About me'>
          <Input.TextArea placeholder='Free format'/>
        </Form.Item>
        <Form.Item className='border-none'
                   onChange={e => setCommunication({...communication, [e.target.value]: e.target.checked})}
                   label='I prefer to communicate by' valuePropName='checked'>
          <div><Checkbox value='E-mail'>E-mail</Checkbox></div>
          <div><Checkbox value='Phone / Messengers'>Phone / Messengers</Checkbox></div>
          <div><Checkbox value='Social media (Facebook, LinkedIn etc.)'>Social media (Facebook, LinkedIn etc.)</Checkbox></div>
        </Form.Item>

        <Form.Item className='border-none'>
          <Button className='blue mt30' htmlType='submit' shape='round'>
            Save changes
          </Button>
        </Form.Item>
      </Form>
      {isModal && <ModalCard reason='сhanges saved' setModal={setIsModal}/>}
    </div>
  );
};

export default connect(({user}) => ({id: user.id}))(Profile);
