import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import {NavLink} from 'react-router-dom';
import cn from 'classnames';
import {HeaderMenu} from '../main';
import {setCurrentPage, CURRENT_PAGE} from '../../../redux/reducers/page';
import {logoWhite, logo} from '../../../common/images/main';
import './index.scss';

const Layout = ({page, isAuth, isMenuShow, setIsMenuShow, setCurrentPage, children}) => {
  const [width, setWidth] = useState(document.documentElement.clientWidth)
  const redirectCurrentPage = (page) => {
    setCurrentPage(CURRENT_PAGE, page)
    setIsMenuShow(false)
  }

  const onResize = e => setWidth(e.target.innerWidth)
  useEffect(() => {

    setWidth(document.documentElement.clientWidth)
    window.addEventListener('resize', onResize)
    return () => {
      window.removeEventListener('resize', onResize)
    }
  }, [])
  return <div className={cn('layout-auth', {'h-auto': page === 'Registration'}, {'bg-register': page === 'Registration'}, {'bg-main': page !== 'Registration'})}>
    <div className='layout-auth__logo'>
      <NavLink to='/'>
        <img src={width > 680 ? logoWhite : logo} alt='logo'/>
      </NavLink>
      {width < 680 && <HeaderMenu page={page} isAuth={isAuth} isMenuShow={isMenuShow} redirectCurrentPage={redirectCurrentPage}/>}
    </div>

    {page === 'Not Found'
      ? {children}
      : <div className={cn('layout-auth__window', {'display-none': !page})}>
        {children}
      </div>
    }
  </div>
}

const mstp = ({page, user}) => ({...page, isAuth: user.isAuth})

export default connect(mstp, {setCurrentPage})(Layout);
